/* Extra small devices (phones, less than 768px) */
@media (max-width: 767px) {

    body{
        padding: 0;
    }

    .sidebar-wrapper {
        position: static;
        width: inherit;
    }

    .main-wrapper {
        padding: 30px;
    }

    .main-wrapper .time {
        //position: static;
        display: block;
        //margin-top: 5px;
    }



    .main-wrapper .upper-row {
        margin-bottom: 0;
    }

    .main-wrapper .section-title {
        margin-left: -16px;
    }

}
/* Small devices (tablets, 768px and up) */
@media (min-width: 768px) {


}

/* Medium devices (desktops, 992px and up) */
@media (max-width: 992px) {
    .projects-section {
        .item {
            display: flex;
            flex-direction: column !important;
            margin-bottom: 15px;
        }

        .demo {
            text-align: center;
            width: 100%;
        }
        .demo img {
            flex: 1;
        }

        div {
            flex: 1
        }
    }
}
@media (min-width: 992px) {

    .skillset .level-title {
        display: inline-block;
        float: left;
        width: 30%;
        margin-bottom: 0;
    }

    .skillset .level-bar {
        display: inline-block;
        width: 70%;
        float: left;
        position: relative;
        top: 1px;
    }


}

